import { Helmet } from 'react-helmet-async';
import { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { Link } from 'react-router-dom';
import { getAllUsers, createUser, updateUser, deleteUser } from '../../lib/api';

export default function AdminUsers() {
  const [page, setPage] = useState(1);
  const [editingUser, setEditingUser] = useState(null);
  const [isCreating, setIsCreating] = useState(false);
  const queryClient = useQueryClient();

  const limit = 20;
  const { data, isLoading } = useQuery({
    queryKey: ['users', page],
    queryFn: () => getAllUsers({ page, limit }),
  });

  const createMutation = useMutation({
    mutationFn: createUser,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['users'] });
      setIsCreating(false);
    },
  });

  const updateMutation = useMutation({
    mutationFn: ({ id, data }) => updateUser(id, data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['users'] });
      setEditingUser(null);
    },
  });

  const deleteMutation = useMutation({
    mutationFn: deleteUser,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['users'] });
    },
  });

  const handleDelete = async (id) => {
    if (window.confirm('Are you sure you want to delete this user?')) {
      deleteMutation.mutate(id);
    }
  };

  const userForm = (user = {}) => (
    <form
      onSubmit={(e) => {
        e.preventDefault();
        const formData = new FormData(e.target);
        const data = {
          name: formData.get('name'),
          email: formData.get('email'),
          role: formData.get('role'),
        };

        if (!user.id) {
          data.password = formData.get('password');
        }

        if (user.id) {
          updateMutation.mutate({ id: user.id, data });
        } else {
          createMutation.mutate(data);
        }
      }}
      className="space-y-4"
    >
      <div>
        <label className="block text-sm font-medium mb-1">Name *</label>
        <input type="text" name="name" defaultValue={user.name} required className="w-full px-4 py-2 border rounded-lg" />
      </div>
      <div>
        <label className="block text-sm font-medium mb-1">Email *</label>
        <input type="email" name="email" defaultValue={user.email} required className="w-full px-4 py-2 border rounded-lg" />
      </div>
      {!user.id && (
        <div>
          <label className="block text-sm font-medium mb-1">Password *</label>
          <input type="password" name="password" required minLength={8} className="w-full px-4 py-2 border rounded-lg" />
        </div>
      )}
      <div>
        <label className="block text-sm font-medium mb-1">Role *</label>
        <select name="role" defaultValue={user.role || 'user'} required className="w-full px-4 py-2 border rounded-lg">
          <option value="user">User</option>
          <option value="admin">Admin</option>
        </select>
      </div>
      <div className="flex gap-4">
        <button type="submit" className="px-6 py-2 bg-accent text-white rounded-lg hover:bg-cyan-600">
          {user.id ? 'Update' : 'Create'}
        </button>
        <button type="button" onClick={() => { setEditingUser(null); setIsCreating(false); }} className="px-6 py-2 bg-gray-200 rounded-lg hover:bg-gray-300">
          Cancel
        </button>
      </div>
    </form>
  );

  return (
    <>
      <Helmet>
        <title>Manage Users  Admin</title>
      </Helmet>

      <div className="min-h-screen bg-gray-50 py-8">
        <div className="container mx-auto px-4">
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-3xl font-bold text-primary">Manage Users</h1>
            <div className="flex gap-4">
              <button
                onClick={() => setIsCreating(true)}
                className="px-4 py-2 bg-accent text-white rounded-lg hover:bg-cyan-600"
              >
                + New User
              </button>
              <Link to="/admin/dashboard" className="text-accent hover:underline">
                 Dashboard
              </Link>
            </div>
          </div>

          {isLoading ? (
            <div className="text-center py-12">Loading...</div>
          ) : (
            <div className="bg-white rounded-lg shadow overflow-hidden">
              <div className="overflow-x-auto">
                <table className="min-w-full">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Name</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Email</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Role</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Created</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-gray-200">
                    {data?.users?.map((user) => (
                      <tr key={user.id}>
                        <td className="px-6 py-4">{user.name}</td>
                        <td className="px-6 py-4">{user.email}</td>
                        <td className="px-6 py-4">
                          <span className={`px-2 py-1 rounded text-xs ${
                            user.role === 'admin' ? 'bg-purple-100 text-purple-800' : 'bg-gray-100 text-gray-800'
                          }`}>
                            {user.role}
                          </span>
                        </td>
                        <td className="px-6 py-4 text-sm text-gray-500">
                          {new Date(user.createdAt).toLocaleDateString()}
                        </td>
                        <td className="px-6 py-4">
                          <button onClick={() => setEditingUser(user)} className="text-accent hover mr-4">
                            Edit
                          </button>
                          <button onClick={() => handleDelete(user.id)} className="text-red-500 hover">
                            Delete
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>

              {data?.pagination && (
                <div className="px-6 py-4 border-t flex justify-between items-center">
                  <span className="text-sm text-gray-600">
                    Page {data.pagination.page} of {data.pagination.pages}
                  </span>
                  <div className="flex gap-2">
                    <button
                      onClick={() => setPage(p => Math.max(1, p - 1))}
                      disabled={page === 1}
                      className="px-4 py-2 border rounded disabled-50"
                    >
                      Previous
                    </button>
                    <button
                      onClick={() => setPage(p => Math.min(data.pagination.pages, p + 1))}
                      disabled={page === data.pagination.pages}
                      className="px-4 py-2 border rounded disabled-50"
                    >
                      Next
                    </button>
                  </div>
                </div>
              )}
            </div>
          )}

          {(editingUser || isCreating) && (
            <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
              <div className="bg-white rounded-lg p-6 max-w-2xl w-full mx-4">
                <h2 className="text-2xl font-bold mb-4">{isCreating ? 'Create User' : 'Edit User'}</h2>
                {userForm(editingUser)}
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
}

