import { Helmet } from 'react-helmet-async';
import { useQuery } from '@tanstack/react-query';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { getAdminStats } from '../../lib/api';
import { useAuth } from '../../contexts/AuthContext';
import { StatCardSkeleton } from '../../components/LoadingSkeleton';

const statIcons = {
  users: '',
  leads: '',
  clients: '',
  projects: '',
  blogs: '',
};

export default function AdminDashboard() {
  const { user, logout } = useAuth();
  const { data: stats, isLoading } = useQuery({
    queryKey: ['adminStats'],
    queryFn: getAdminStats,
  });

  const statCards = [
    { key: 'users', label: 'Total Users', value: stats?.stats.users || 0, color: 'from-blue-500 to-cyan-500' },
    { key: 'leads', label: 'Contact Requests', value: stats?.stats.leads || 0, color: 'from-purple-500 to-pink-500' },
    { key: 'clients', label: 'Total Clients', value: stats?.stats.clients || 0, color: 'from-green-500 to-emerald-500' },
    { key: 'projects', label: 'Total Projects', value: stats?.stats.projects || 0, color: 'from-orange-500 to-red-500' },
    { key: 'blogs', label: 'Blog Posts', value: stats?.stats.blogs || 0, color: 'from-indigo-500 to-purple-500' },
  ];

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-50 via-white to-gray-50 py-8">
        <div className="container mx-auto px-4">
          <div className="grid grid-cols-1 md:grid-cols-5 gap-6">
            {[1, 2, 3, 4, 5].map((i) => (
              <StatCardSkeleton key={i} />
            ))}
          </div>
        </div>
      </div>
    );
  }

  return (
    <>
      <Helmet>
        <title>Admin Dashboard  Biogleam</title>
      </Helmet>

      <div className="min-h-screen bg-gradient-to-br from-gray-50 via-white to-gray-50 py-8">
        <div className="container mx-auto px-4">
          {/* Header */}
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            className="flex justify-between items-center mb-8"
          >
            <div>
              <h1 className="text-4xl font-bold gradient-text mb-2">Admin Dashboard</h1>
              <p className="text-gray-600">Welcome back, <span className="font-semibold text-primary">{user?.name}</span></p>
            </div>
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={logout}
              className="px-6 py-3 bg-gradient-to-r from-red-500 to-red-600 text-white rounded-xl font-semibold shadow-lg hover:shadow-xl transition-shadow"
            >
              Logout
            </motion.button>
          </motion.div>

          {stats && (
            <>
              {/* Stats Grid */}
              <div className="grid grid-cols-1 md:grid-cols-5 gap-6 mb-8">
                {statCards.map((stat, index) => (
                  <motion.div
                    key={stat.key}
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: index * 0.1 }}
                    whileHover={{ y: -8, scale: 1.02 }}
                    className="card p-6 relative overflow-hidden group"
                  >
                    <div className={`absolute inset-0 bg-gradient-to-br ${stat.color} opacity-0 group-hover:opacity-10 transition-opacity duration-300`} />
                    <div className="relative z-10">
                      <div className="flex items-center justify-between mb-4">
                        <span className="text-4xl">{statIcons[stat.key]}</span>
                        <div className={`w-12 h-12 rounded-lg bg-gradient-to-br ${stat.color} opacity-20 group-hover:opacity-30 transition-opacity`} />
                      </div>
                      <h3 className="text-gray-600 text-sm mb-2 font-medium">{stat.label}</h3>
                      <p className="text-4xl font-bold text-primary">{stat.value}</p>
                    </div>
                  </motion.div>
                ))}
              </div>

              {/* Quick Links and Status */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
                {/* Quick Links */}
                <motion.div
                  initial={{ opacity: 0, x: -20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ delay: 0.3 }}
                  className="card p-6"
                >
                  <h3 className="text-xl font-bold text-primary mb-6 flex items-center gap-2">
                    <span></span> Quick Links
                  </h3>
                  <div className="space-y-3">
                    {[
                      { to: '/admin/contact-requests', label: 'Manage Contact Requests', icon: '' },
                      { to: '/admin/clients', label: 'Manage Clients', icon: '' },
                      { to: '/admin/projects', label: 'Manage Projects', icon: '' },
                      { to: '/admin/blogs', label: 'Manage Blog Posts', icon: '' },
                      { to: '/admin/users', label: 'Manage Users', icon: '' },
                    ].map((link, index) => (
                      <motion.div
                        key={link.to}
                        initial={{ opacity: 0, x: -10 }}
                        animate={{ opacity: 1, x: 0 }}
                        transition={{ delay: 0.4 + index * 0.05 }}
                      >
                        <Link
                          to={link.to}
                          className="flex items-center gap-3 p-3 rounded-lg hover:bg-gray-50 transition-colors group"
                        >
                          <span className="text-xl">{link.icon}</span>
                          <span className="text-gray-700 group-hover:text-accent font-medium flex-1">{link.label}</span>
                          <motion.span
                            className="text-accent"
                            animate={{ x: [0, 5, 0] }}
                            transition={{
                              duration: 1.5,
                              repeat: Infinity,
                              ease: 'easeInOut',
                            }}
                          >
                            
                          </motion.span>
                        </Link>
                      </motion.div>
                    ))}
                  </div>
                </motion.div>

                {/* Status Chart */}
                <motion.div
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ delay: 0.3 }}
                  className="card p-6"
                >
                  <h3 className="text-xl font-bold text-primary mb-6 flex items-center gap-2">
                    <span></span> Contact Requests by Status
                  </h3>
                  <div className="space-y-4">
                    {Object.entries(stats.leadsByStatus).map(([status, count], index) => {
                      const total = Object.values(stats.leadsByStatus).reduce((a, b) => a + b, 0);
                      const percentage = total > 0 ? (count / total) * 100 : 0;
                      const statusColors = {
                        converted: 'from-green-500 to-emerald-500',
                        qualified: 'from-blue-500 to-cyan-500',
                        contacted: 'from-yellow-500 to-orange-500',
                        created: 'from-gray-400 to-gray-500',
                      };
                      
                      return (
                        <motion.div
                          key={status}
                          initial={{ opacity: 0, x: 10 }}
                          animate={{ opacity: 1, x: 0 }}
                          transition={{ delay: 0.4 + index * 0.05 }}
                        >
                          <div className="flex justify-between items-center mb-2">
                            <span className="capitalize font-medium text-gray-700">{status}</span>
                            <span className="font-bold text-primary">{count}</span>
                          </div>
                          <div className="h-2 bg-gray-200 rounded-full overflow-hidden">
                            <motion.div
                              className={`h-full bg-gradient-to-r ${statusColors[status] || statusColors.created} rounded-full`}
                              initial={{ width: 0 }}
                              animate={{ width: `${percentage}%` }}
                              transition={{ delay: 0.5 + index * 0.05, duration: 0.5 }}
                            />
                          </div>
                        </motion.div>
                      );
                    })}
                  </div>
                </motion.div>
              </div>

              {/* Recent Leads */}
              {stats.recentLeads && stats.recentLeads.length > 0 && (
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: 0.5 }}
                  className="card p-6"
                >
                  <h3 className="text-xl font-bold text-primary mb-6 flex items-center gap-2">
                    <span></span> Recent Contact Requests
                  </h3>
                  <div className="overflow-x-auto">
                    <table className="min-w-full">
                      <thead>
                        <tr className="border-b border-gray-200">
                          <th className="text-left p-3 text-sm font-semibold text-gray-600">Name</th>
                          <th className="text-left p-3 text-sm font-semibold text-gray-600">Phone</th>
                          <th className="text-left p-3 text-sm font-semibold text-gray-600">Project Type</th>
                          <th className="text-left p-3 text-sm font-semibold text-gray-600">Status</th>
                          <th className="text-left p-3 text-sm font-semibold text-gray-600">Date</th>
                        </tr>
                      </thead>
                      <tbody>
                        {stats.recentLeads.map((lead, index) => (
                          <motion.tr
                            key={lead.id}
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            transition={{ delay: 0.6 + index * 0.05 }}
                            className="border-b border-gray-100 hover:bg-gray-50 transition-colors"
                          >
                            <td className="p-3 font-medium">{lead.name}</td>
                            <td className="p-3 text-gray-600">{lead.phone}</td>
                            <td className="p-3 text-gray-600">{lead.projectType || '-'}</td>
                            <td className="p-3">
                              <span className={`px-3 py-1 rounded-full text-xs font-semibold ${
                                lead.status === 'converted' ? 'bg-green-100 text-green-800' :
                                lead.status === 'qualified' ? 'bg-blue-100 text-blue-800' :
                                lead.status === 'contacted' ? 'bg-yellow-100 text-yellow-800' :
                                'bg-gray-100 text-gray-800'
                              }`}>
                                {lead.status}
                              </span>
                            </td>
                            <td className="p-3 text-gray-600 text-sm">{new Date(lead.createdAt).toLocaleDateString()}</td>
                          </motion.tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </motion.div>
              )}
            </>
          )}
        </div>
      </div>
    </>
  );
}
