import { Helmet } from 'react-helmet-async';
import { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { Link } from 'react-router-dom';
import { getAllLeads, updateLead, deleteLead } from '../../lib/api';

export default function AdminContactRequests() {
  const [page, setPage] = useState(1);
  const [statusFilter, setStatusFilter] = useState('');
  const [search, setSearch] = useState('');
  const [editingRequest, setEditingRequest] = useState(null);
  const queryClient = useQueryClient();
  const limit = 20;

  const { data, isLoading } = useQuery({
    queryKey: ['contactRequests', page, statusFilter, search],
    queryFn: () =>
      getAllLeads({
        page,
        limit,
        status: statusFilter || undefined,
        search: search || undefined,
      }),
  });

  const updateMutation = useMutation({
    mutationFn: ({ id, data }) =>
      updateLead(id, data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['contactRequests'] });
      queryClient.invalidateQueries({ queryKey: ['adminStats'] });
      setEditingRequest(null);
    },
  });

  const deleteMutation = useMutation({
    mutationFn: (id) => deleteLead(id),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['contactRequests'] });
      queryClient.invalidateQueries({ queryKey: ['adminStats'] });
    },
  });

  const handleDelete = (id) => {
    if (window.confirm('Are you sure you want to delete this contact request?')) {
      deleteMutation.mutate(id);
    }
  };

  const handleStatusChange = (id, status) => {
    updateMutation.mutate({ id, data: { status } });
  };

  const handleEditSubmit = (e) => {
    e.preventDefault();
    if (!editingRequest) return;
    const formData = new FormData(e.currentTarget);

    const data = {
      name: formData.get('name'),
      email: formData.get('email') || undefined,
      phone: formData.get('phone'),
      projectType: formData.get('projectType') || undefined,
      packageType: formData.get('packageType') || undefined,
      message: formData.get('message'),
      status: formData.get('status') || 'created',
    };

    updateMutation.mutate({
      id: editingRequest.id,
      data,
    });
  };

  const requests = data?.leads || [];
  const pagination = data?.pagination;

  return (
    <>
      <Helmet>
        <title>Contact Requests  Admin</title>
      </Helmet>

      <div className="min-h-screen bg-gray-50 py-8">
        <div className="container mx-auto px-4">
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-3xl font-bold text-primary">Contact Requests</h1>
            <Link to="/admin/dashboard" className="text-accent hover">
               Back to Dashboard
            </Link>
          </div>

          <div className="bg-white rounded-lg shadow p-6 mb-6">
            <div className="grid grid-cols-1 md-cols-3 gap-4">
              <input
                type="text"
                placeholder="Search contact requests..."
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                className="px-4 py-2 border rounded-lg"
              />
              <select
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value)}
                className="px-4 py-2 border rounded-lg"
              >
                <option value="">All Statuses</option>
                <option value="created">Created</option>
                <option value="contacted">Contacted</option>
                <option value="qualified">Qualified</option>
                <option value="converted">Converted</option>
                <option value="lost">Lost</option>
              </select>
            </div>
          </div>

          {isLoading ? (
            <div className="text-center py-12">Loading contact requests...</div>
          ) : (
            <div className="bg-white rounded-lg shadow overflow-hidden">
              <div className="overflow-x-auto">
                <table className="min-w-full">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">
                        Client Name
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">
                        Phone
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">
                        Email
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">
                        Project Type
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">
                        Package
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">
                        Status
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">
                        Date
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">
                        Actions
                      </th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-gray-200">
                    {requests.map((req) => (
                      <tr key={req.id}>
                        <td className="px-6 py-4 whitespace-nowrap">{req.name}</td>
                        <td className="px-6 py-4 whitespace-nowrap">{req.phone}</td>
                        <td className="px-6 py-4 whitespace-nowrap">{req.email || '-'}</td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          {req.projectType || req.project_type || '-'}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          {req.packageType || req.package_type || '-'}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <select
                            value={req.status}
                            onChange={(e) => handleStatusChange(req.id, e.target.value)}
                            className="text-xs px-2 py-1 rounded border"
                          >
                            <option value="created">Created</option>
                            <option value="contacted">Contacted</option>
                            <option value="qualified">Qualified</option>
                            <option value="converted">Converted</option>
                            <option value="lost">Lost</option>
                          </select>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {new Date(req.createdAt || req.created_at).toLocaleDateString()}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <button
                            onClick={() => setEditingRequest(req)}
                            className="text-accent hover mr-4"
                          >
                            Edit
                          </button>
                          <button
                            onClick={() => handleDelete(req.id)}
                            className="text-red-500 hover"
                          >
                            Delete
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>

              {pagination && (
                <div className="px-6 py-4 border-t flex justify-between items-center">
                  <span className="text-sm text-gray-600">
                    Page {pagination.page} of {pagination.pages}
                  </span>
                  <div className="flex gap-2">
                    <button
                      onClick={() => setPage((p) => Math.max(1, p - 1))}
                      disabled={page === 1}
                      className="px-4 py-2 border rounded disabled-50"
                    >
                      Previous
                    </button>
                    <button
                      onClick={() => setPage((p) => Math.min(pagination.pages, p + 1))}
                      disabled={page === pagination.pages}
                      className="px-4 py-2 border rounded disabled-50"
                    >
                      Next
                    </button>
                  </div>
                </div>
              )}
            </div>
          )}

          {editingRequest && (
            <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
              <div className="bg-white rounded-lg p-6 max-w-2xl w-full mx-4 max-h-[90vh] overflow-y-auto">
                <h2 className="text-2xl font-bold mb-4">Edit Contact Request</h2>
                <form onSubmit={handleEditSubmit} className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium mb-1">Client Name *</label>
                    <input
                      type="text"
                      name="name"
                      defaultValue={editingRequest.name}
                      required
                      className="w-full px-4 py-2 border rounded-lg"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium mb-1">Client Phone *</label>
                    <input
                      type="tel"
                      name="phone"
                      defaultValue={editingRequest.phone}
                      required
                      className="w-full px-4 py-2 border rounded-lg"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium mb-1">Client Email</label>
                    <input
                      type="email"
                      name="email"
                      defaultValue={editingRequest.email || ''}
                      className="w-full px-4 py-2 border rounded-lg"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium mb-1">Project Type</label>
                    <input
                      type="text"
                      name="projectType"
                      defaultValue={editingRequest.projectType || editingRequest.project_type || ''}
                      className="w-full px-4 py-2 border rounded-lg"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium mb-1">Package</label>
                    <select
                      name="packageType"
                      defaultValue={editingRequest.packageType || editingRequest.package_type || ''}
                      className="w-full px-4 py-2 border rounded-lg"
                    >
                      <option value="">None</option>
                      <option value="starter">Starter</option>
                      <option value="pro">Pro</option>
                      <option value="enterprise">Enterprise</option>
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium mb-1">Status</label>
                    <select
                      name="status"
                      defaultValue={editingRequest.status}
                      className="w-full px-4 py-2 border rounded-lg"
                    >
                      <option value="created">Created</option>
                      <option value="contacted">Contacted</option>
                      <option value="qualified">Qualified</option>
                      <option value="converted">Converted</option>
                      <option value="lost">Lost</option>
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium mb-1">Client Message</label>
                    <textarea
                      name="message"
                      defaultValue={editingRequest.message}
                      rows={4}
                      className="w-full px-4 py-2 border rounded-lg"
                    />
                  </div>
                  <div className="flex gap-4">
                    <button
                      type="submit"
                      className="px-6 py-2 bg-accent text-white rounded-lg hover:bg-cyan-600"
                    >
                      Save
                    </button>
                    <button
                      type="button"
                      onClick={() => setEditingRequest(null)}
                      className="px-6 py-2 bg-gray-200 rounded-lg hover:bg-gray-300"
                    >
                      Cancel
                    </button>
                  </div>
                </form>
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
}


