import { Helmet } from 'react-helmet-async';
import ServiceCard from '../components/ServiceCard';

export default function Services() {
  return (
    <>
      <Helmet>
        <title>Services  Biogleam</title>
        <meta
          name="description"
          content="Biogleam offers custom website solutions for restaurants, hotels, e-commerce, fitness, interior designers, photo studios, and more."
        />
      </Helmet>

      <section className="py-16">
        <div className="container mx-auto px-4">
          <div className="text-center mb-12">
            <h1 className="text-4xl md:text-5xl font-bold text-primary mb-4">Our Services</h1>
            <p className="text-xl text-gray-600 max-w-2xl mx-auto">
              We specialize in building custom websites and web applications for specific
              industries and use cases.
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-8 max-w-5xl mx-auto">
            {[
              {
                title: 'Restaurant Websites',
                description: 'Complete online presence for restaurants with ordering, reservations, and more.',
                features: [
                  'Online ordering system',
                  'POS integration',
                  'Menu management',
                  'Reservation system',
                  'Delivery tracking',
                ],
              },
              {
                title: 'Interior Design Portfolios',
                description: 'Showcase your design work and attract new clients.',
                features: [
                  'Portfolio showcase',
                  'Client galleries',
                  'Booking system',
                  'Project management',
                  'Before/after galleries',
                ],
              },
              {
                title: 'Photo Studio Websites',
                description: 'Professional websites for photographers and studios.',
                features: [
                  'Online booking',
                  'Image galleries',
                  'Client proofing',
                  'Digital delivery',
                  'Payment processing',
                ],
              },
              {
                title: 'Hotel Booking Platforms',
                description: 'Complete booking and management systems for hotels.',
                features: [
                  'Online booking system',
                  'Room availability calendar',
                  'Payment processing',
                  'Guest management',
                  'Review and rating system',
                ],
              },
              {
                title: 'E-commerce Websites',
                description: 'Full-featured online stores for any business.',
                features: [
                  'Product catalog management',
                  'Shopping cart & checkout',
                  'Payment gateway integration',
                  'Order management system',
                  'Inventory tracking',
                ],
              },
              {
                title: 'Fitness Platforms',
                description: 'Comprehensive solutions for gyms and fitness centers.',
                features: [
                  'Class scheduling system',
                  'Member management portal',
                  'Workout plan builder',
                  'Progress tracking',
                  'Online booking & payments',
                ],
              },
              {
                title: 'Custom Web Applications',
                description: 'Tailored solutions for unique business needs.',
                features: [
                  'Custom dashboards',
                  'CRM systems',
                  'CMS integrations',
                  'API development',
                  'Third-party integrations',
                ],
              },
            ].map((service, index) => (
              <ServiceCard key={service.title} index={index} {...service} />
            ))}
          </div>
        </div>
      </section>
    </>
  );
}
