import { Helmet } from 'react-helmet-async';

export default function About() {
  return (
    <>
      <Helmet>
        <title>About  Biogleam</title>
        <meta
          name="description"
          content="Learn about Biogleam  we design, build, and launch sites that grow businesses."
        />
      </Helmet>

      <section className="py-16">
        <div className="container mx-auto px-4 max-w-4xl">
          <h1 className="text-4xl md:text-5xl font-bold text-primary mb-8">
            We design, build, and launch sites that grow businesses.
          </h1>

          <div className="prose prose-lg max-w-none">
            <p className="text-gray-700 text-lg mb-6">
              Biogleam is a web studio specializing in custom websites and web applications for
              restaurants, hotels, e-commerce stores, fitness centers, interior designers, photo studios, and creative businesses. We combine
              thoughtful design with robust development to deliver digital experiences that drive
              results.
            </p>

            <p className="text-gray-700 text-lg mb-6">
              Our approach is built on three core values: <strong>craft</strong>  we take pride in
              clean, maintainable code and pixel-perfect design; <strong>reliability</strong>  we
              deliver on time and provide ongoing support; and <strong>ROI</strong>  every feature
              we build is designed to help your business grow.
            </p>

            <p className="text-gray-700 text-lg mb-6">
              Whether you need an online ordering system for your restaurant, a booking platform for
              your hotel, an e-commerce store for your business, a fitness platform for your gym, a portfolio site for your interior design studio, or a booking system for
              your photo studio, we handle everything from initial design to deployment and maintenance.
            </p>

            <h2 className="text-2xl font-bold text-primary mt-12 mb-4">Our Capabilities</h2>
            <ul className="list-disc list-inside text-gray-700 space-y-2 mb-6">
              <li>Custom web design and development</li>
              <li>E-commerce and online ordering systems</li>
              <li>Booking and reservation platforms</li>
              <li>Content management systems</li>
              <li>API integrations (POS, payment gateways, etc.)</li>
              <li>Performance optimization and SEO</li>
              <li>Hosting, deployment, and ongoing maintenance</li>
            </ul>
          </div>
        </div>
      </section>
    </>
  );
}
