import { Helmet } from 'react-helmet-async';
import { useState, useEffect } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { Link } from 'react-router-dom';
import { getBlogPosts, createBlogPost, updateBlogPost, deleteBlogPost, getCurrentUser } from '../../lib/api';
import ImageUpload from '../../components/ImageUpload';

export default function AdminBlogs() {
  const [page, setPage] = useState(1);
  const [editingPost, setEditingPost] = useState(null);
  const [isCreating, setIsCreating] = useState(false);
  const [authorId, setAuthorId] = useState(null);
  const queryClient = useQueryClient();
  const [featuredImageUrl, setFeaturedImageUrl] = useState('');

  const { data: userData } = useQuery({
    queryKey: ['currentUser'],
    queryFn: getCurrentUser,
  });

  useEffect(() => {
    if (userData?.user) {
      setAuthorId(userData.user.id);
    }
  }, [userData]);

  const { data, isLoading } = useQuery({
    queryKey: ['blogPosts', page],
    queryFn: () => getBlogPosts(),
  });

  const posts = data || [];

  const createMutation = useMutation({
    mutationFn: createBlogPost,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['blogPosts'] });
      setIsCreating(false);
    },
  });

  const updateMutation = useMutation({
    mutationFn: ({ id, data }) => updateBlogPost(id, data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['blogPosts'] });
      setEditingPost(null);
    },
  });

  const deleteMutation = useMutation({
    mutationFn: deleteBlogPost,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['blogPosts'] });
    },
  });

  const handleDelete = async (id) => {
    if (window.confirm('Are you sure you want to delete this blog post?')) {
      deleteMutation.mutate(id);
    }
  };

  const PostForm = ({ post, onClose }) => {
    const handleImageUpload = (url) => {
      setFeaturedImageUrl(url);
      // Optionally insert image into content
      const contentTextarea = document.querySelector('textarea[name="content"]');
      if (contentTextarea) {
        const cursorPos = contentTextarea.selectionStart;
        const textBefore = contentTextarea.value.substring(0, cursorPos);
        const textAfter = contentTextarea.value.substring(cursorPos);
        const imageTag = `<img src="${url}" alt="Featured image" style="max-width: 100%; height: auto;" />`;
        contentTextarea.value = textBefore + imageTag + textAfter;
        contentTextarea.dispatchEvent(new Event('input', { bubbles: true }));
      }
    };

    return (
      <form
        onSubmit={(e) => {
          e.preventDefault();
          const formData = new FormData(e.target);
          const tagsStr = formData.get('tags');
          const data = {
            slug: formData.get('slug'),
            title: formData.get('title'),
            summary: formData.get('summary') || undefined,
            content: formData.get('content') || undefined,
            authorId: authorId?.toString(),
            tags: tagsStr ? tagsStr.split(',').map(s => s.trim()) : undefined,
            publishedAt: formData.get('publishedAt') || undefined,
          };

          if (post.id) {
            updateMutation.mutate({ id: post.id, data });
          } else {
            createMutation.mutate(data);
          }
        }}
        className="space-y-4"
      >
        <div>
          <label className="block text-sm font-medium mb-1">Slug *</label>
          <input type="text" name="slug" defaultValue={post.slug} required className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Title *</label>
          <input type="text" name="title" defaultValue={post.title} required className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Summary</label>
          <textarea name="summary" defaultValue={post.summary} rows={3} className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Featured Image</label>
          <ImageUpload
            onUpload={handleImageUpload}
          />
          {featuredImageUrl && (
            <div className="mt-2">
              <img src={featuredImageUrl} alt="Featured" className="max-w-xs h-auto rounded-lg border" />
              <p className="text-xs text-gray-500 mt-1">Image URL: {featuredImageUrl}</p>
            </div>
          )}
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Content (HTML)</label>
          <textarea name="content" defaultValue={post.content} rows={10} className="w-full px-4 py-2 border rounded-lg" />
          <p className="text-xs text-gray-500 mt-1">Tip: Upload an image above to automatically insert it into the content</p>
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Tags (comma-separated)</label>
          <input type="text" name="tags" defaultValue={post.tags?.join(', ')} className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Published At (ISO datetime)</label>
          <input type="datetime-local" name="publishedAt" defaultValue={post.publishedAt ? new Date(post.publishedAt).toISOString().slice(0, 16) : ''} className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div className="flex gap-4">
          <button type="submit" className="px-6 py-2 bg-accent text-white rounded-lg hover:bg-cyan-600">
            {post.id ? 'Update' : 'Create'}
          </button>
          <button type="button" onClick={onClose} className="px-6 py-2 bg-gray-200 rounded-lg hover:bg-gray-300">
            Cancel
          </button>
        </div>
      </form>
    );
  };

  return (
    <>
      <Helmet>
        <title>Manage Blog Posts  Admin</title>
      </Helmet>

      <div className="min-h-screen bg-gray-50 py-8">
        <div className="container mx-auto px-4">
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-3xl font-bold text-primary">Manage Blog Posts</h1>
            <div className="flex gap-4">
              <button
                onClick={() => setIsCreating(true)}
                className="px-4 py-2 bg-accent text-white rounded-lg hover:bg-cyan-600"
              >
                + New Post
              </button>
              <Link to="/admin/dashboard" className="text-accent hover:underline">
                 Dashboard
              </Link>
            </div>
          </div>

          {isLoading ? (
            <div className="text-center py-12">Loading...</div>
          ) : (
            <div className="bg-white rounded-lg shadow overflow-hidden">
              <div className="overflow-x-auto">
                <table className="min-w-full">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Title</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Slug</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Published</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-gray-200">
                    {posts.map((post) => (
                      <tr key={post.id}>
                        <td className="px-6 py-4">{post.title}</td>
                        <td className="px-6 py-4">{post.slug}</td>
                        <td className="px-6 py-4">
                          {post.publishedAt ? new Date(post.publishedAt).toLocaleDateString() : 'Draft'}
                        </td>
                        <td className="px-6 py-4">
                          <button onClick={() => setEditingPost(post)} className="text-accent hover:underline mr-4">
                            Edit
                          </button>
                          <button onClick={() => handleDelete(post.id)} className="text-red-500 hover:underline">
                            Delete
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}

          {(editingPost || isCreating) && (
            <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
              <div className="bg-white rounded-lg p-6 max-w-4xl w-full mx-4 max-h-[90vh] overflow-y-auto">
                <h2 className="text-2xl font-bold mb-4">{isCreating ? 'Create Blog Post' : 'Edit Blog Post'}</h2>
                <PostForm
                  post={editingPost || {}}
                  onClose={() => {
                    setEditingPost(null);
                    setIsCreating(false);
                  }}
                />
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
}
