import { Helmet } from 'react-helmet-async';
import { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { Link } from 'react-router-dom';
import { getProjects, createProject, updateProject, deleteProject } from '../../lib/api';
import ImageUpload from '../../components/ImageUpload';

export default function AdminProjects() {
  const [page, setPage] = useState(1);
  const [search, setSearch] = useState('');
  const [editingProject, setEditingProject] = useState(null);
  const [isCreating, setIsCreating] = useState(false);
  const queryClient = useQueryClient();

  const { data, isLoading } = useQuery({
    queryKey: ['projects', page, search],
    queryFn: () => getProjects(),
  });

  const projects = data || [];

  const createMutation = useMutation({
    mutationFn: createProject,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['projects'] });
      setIsCreating(false);
    },
  });

  const updateMutation = useMutation({
    mutationFn: ({ id, data }) => updateProject(id, data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['projects'] });
      setEditingProject(null);
    },
  });

  const deleteMutation = useMutation({
    mutationFn: deleteProject,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['projects'] });
    },
  });

  const handleDelete = async (id) => {
    if (window.confirm('Are you sure you want to delete this project?')) {
      deleteMutation.mutate(id);
    }
  };

  const ProjectForm = ({ project, onClose }) => {
    const [projectImages, setProjectImages] = useState(project?.images || []);

    return (
      <form
        onSubmit={(e) => {
          e.preventDefault();
          const formData = new FormData(e.target);
          const data = {
            slug: formData.get('slug'),
            title: formData.get('title'),
            summary: formData.get('summary') || undefined,
            content: formData.get('content') || undefined,
            images: projectImages.length > 0 ? projectImages : undefined,
            client: formData.get('client') || undefined,
            publishedAt: formData.get('publishedAt') || undefined,
          };

          if (project.id) {
            updateMutation.mutate({ id: project.id, data });
          } else {
            createMutation.mutate(data);
          }
        }}
        className="space-y-4"
      >
        <div>
          <label className="block text-sm font-medium mb-1">Slug *</label>
          <input type="text" name="slug" defaultValue={project.slug} required className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Title *</label>
          <input type="text" name="title" defaultValue={project.title} required className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Summary</label>
          <textarea name="summary" defaultValue={project.summary} rows={3} className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Content (HTML)</label>
          <textarea name="content" defaultValue={project.content} rows={10} className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Images</label>
          <ImageUpload
            multiple
            maxImages={10}
            existingImages={projectImages}
            onImagesChange={setProjectImages}
            onMultipleUpload={(urls) => {
              setProjectImages([...projectImages, ...urls]);
            }}
          />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Client</label>
          <input type="text" name="client" defaultValue={project.client} className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Published At (ISO datetime)</label>
          <input type="datetime-local" name="publishedAt" defaultValue={project.publishedAt ? new Date(project.publishedAt).toISOString().slice(0, 16) : ''} className="w-full px-4 py-2 border rounded-lg" />
        </div>
        <div className="flex gap-4">
          <button type="submit" className="px-6 py-2 bg-accent text-white rounded-lg hover:bg-cyan-600">
            {project.id ? 'Update' : 'Create'}
          </button>
          <button type="button" onClick={onClose} className="px-6 py-2 bg-gray-200 rounded-lg hover:bg-gray-300">
            Cancel
          </button>
        </div>
      </form>
    );
  };

  return (
    <>
      <Helmet>
        <title>Manage Projects  Admin</title>
      </Helmet>

      <div className="min-h-screen bg-gray-50 py-8">
        <div className="container mx-auto px-4">
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-3xl font-bold text-primary">Manage Projects</h1>
            <div className="flex gap-4">
              <button
                onClick={() => setIsCreating(true)}
                className="px-4 py-2 bg-accent text-white rounded-lg hover:bg-cyan-600"
              >
                + New Project
              </button>
              <Link to="/admin/dashboard" className="text-accent hover:underline">
                 Dashboard
              </Link>
            </div>
          </div>

          {isLoading ? (
            <div className="text-center py-12">Loading...</div>
          ) : (
            <div className="bg-white rounded-lg shadow overflow-hidden">
              <div className="overflow-x-auto">
                <table className="min-w-full">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Title</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Slug</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Client</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Published</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-gray-200">
                    {projects.map((project) => (
                      <tr key={project.id}>
                        <td className="px-6 py-4">{project.title}</td>
                        <td className="px-6 py-4">{project.slug}</td>
                        <td className="px-6 py-4">{project.client || '-'}</td>
                        <td className="px-6 py-4">
                          {project.publishedAt ? new Date(project.publishedAt).toLocaleDateString() : 'Draft'}
                        </td>
                        <td className="px-6 py-4">
                          <button onClick={() => setEditingProject(project)} className="text-accent hover:underline mr-4">
                            Edit
                          </button>
                          <button onClick={() => handleDelete(project.id)} className="text-red-500 hover:underline">
                            Delete
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}

          {(editingProject || isCreating) && (
            <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
              <div className="bg-white rounded-lg p-6 max-w-4xl w-full mx-4 max-h-[90vh] overflow-y-auto">
                <h2 className="text-2xl font-bold mb-4">{isCreating ? 'Create Project' : 'Edit Project'}</h2>
                <ProjectForm
                  project={editingProject || {}}
                  onClose={() => {
                    setEditingProject(null);
                    setIsCreating(false);
                  }}
                />
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
}
