import { Helmet } from 'react-helmet-async';
import { useQuery } from '@tanstack/react-query';
import { getProjects } from '../lib/api';
import ProjectCard from '../components/ProjectCard';

export default function Projects() {
  const { data = [], isLoading } = useQuery({
    queryKey: ['projects'],
    queryFn: getProjects,
  });

  const projects = data || [];

  return (
    <>
      <Helmet>
        <title>Projects  Biogleam</title>
        <meta name="description" content="View our portfolio of custom websites and web applications." />
      </Helmet>

      <section className="py-16">
        <div className="container mx-auto px-4">
          <div className="text-center mb-12">
            <h1 className="text-4xl md:text-5xl font-bold text-primary mb-4">Our Projects</h1>
            <p className="text-xl text-gray-600 max-w-2xl mx-auto">
              Explore case studies from our recent work with restaurants, hotels, e-commerce stores, fitness centers, interior designers, and
              photo studios.
            </p>
          </div>

          {isLoading ? (
            <div className="text-center py-12">
              <p className="text-gray-600">Loading projects...</p>
            </div>
          ) : projects.length === 0 ? (
            <div className="text-center py-12">
              <p className="text-gray-600">No projects available yet. Check back soon!</p>
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {projects.map((project, index) => (
                <ProjectCard
                  key={project.id}
                  index={index}
                  slug={project.slug}
                  title={project.title}
                  summary={project.summary || ''}
                  image={project.images?.[0]}
                  client={project.client}
                />
              ))}
            </div>
          )}
        </div>
      </section>
    </>
  );
}
